  get crashPulse() {
    let impactConf: { [key: string]: number } = {
      F2F: 0,
      F2S: 0,
      F2B: 0,
      NO: 0,
      WO: 0,
    }; // default
    let impactOverlap: { [key: string]: number } = { LO: 0, SO: 0 }; // default

    // set dynamically the impact configuration
    impactConf[this.impactConf] = 1;
    impactOverlap[this.impactOverlap] = 1;

    const crashPulseDuration =
      109.6 +
      2.9 * impactOverlap.SO -
      4.6 * impactConf.F2S +
      7.6 * impactConf.F2B +
      6.1 * impactConf.NO;

    const EV1 =
      1.53 -
      8.81 * this.vehicleMass * 10 ** -4 -
      5.32 * impactConf.F2S * 10 ** -1 +
      1.02 * impactConf.NO * 10 ** -1;

    const EV2 =
      -2.62 * this.deltaV * 10 ** -2 +
      4.73 * this.vehicleMass * 10 ** -4 +
      3.75 * impactConf.F2B * 10 ** -1;

    const EV3 =
      -0.17 -
      0.84 * this.deltaV * 10 ** -2 -
      2.56 * impactConf.F2S * 10 ** -1 -
      3.95 * impactConf.WO * 10 ** -1;

    const EV4 =
      4.63 * this.PDOF * 10 ** -3 +
      1.41 * impactConf.F2B * 10 ** -1 +
      2.15 * 10 ** -1;

    const EV6 =
      -0.26 * this.vehicleMass * 10 ** -4 + 2.22 * impactConf.F2B * 10 ** -1;

    let crashPulse: number[] = [];

    for (
      let index = 0;
      index < this.modelEigenVectors.average.length;
      index++
    ) {
      crashPulse.push(
        ((((1 / 3.6) * this.deltaV) / crashPulseDuration) *
          (this.modelEigenVectors.average[index] +
            EV1 * this.modelEigenVectors.EV1[index] +
            EV2 * this.modelEigenVectors.EV2[index] +
            EV3 * this.modelEigenVectors.EV3[index] +
            EV4 * this.modelEigenVectors.EV4[index] +
            EV6 * this.modelEigenVectors.EV6[index]) *
          10 ** 3) /
          9.81
      );
    }
    return {
      x: this.modelEigenVectors.time.map((val) => val * crashPulseDuration),
      y: crashPulse,
    };
  }

  get plottingLayout() {
    return {
      title: "Crash pulse",
      xaxis: {
        title: "Time [ms]",
        showgrid: true,
        zeroline: false,
      },
      yaxis: {
        title: "Longitudinal acceleration [g]",
        showline: false,
      },
    };
  }

  get plottingData() {
    return [
      {
        x: this.crashPulse.x,
        y: this.crashPulse.y,
        type: "scatter",
        name: "Crash pulse",
      },
    ];
  }
